<template>
  <div>
    <div class="set-meal-title">
      <i class="el-icon-arrow-left back-icon" @click="back" style="cursor: pointer"></i>
      選擇套餐
    </div>
    <el-row class="thumbnail-box">
      <el-col :span="8">
        <div class="thumbnail-img">

          <!--缩略图-->
        </div>
      </el-col>
      <el-col :span="16">
        <div class="price-box">
          <div class="sm-price">
            {{comDatas.currency_front_symbol}}
            <span>{{comDatas.price}}</span>
          </div>
        </div>
        <div class="num-box">
          <el-input-number v-model="num1" @change="handleChange" :min="1" :max="10" label="数量"></el-input-number>
        </div>
      </el-col>

    </el-row>

    <div class="format">
      <div class="package">
        <div class="package-title">套餐</div>
        <div class="set-meal-box">
          <el-button v-for="(sm,index) in smDatas" :key="index" class="set-meal-btn" @click="selectSetMeal(index)">{{ sm.setname }}</el-button>
        </div>
      </div>
      <div class="package">
        <div class="package-title">規格</div>
        <div class="set-meal-box attr-box">
          <div class="attr-title">

          </div>
        </div>
      </div>
    </div>

    <div>
      <!--<router-link :to="{ name : 'form', params : { smString: smString}}">-->
        <!--<div class="buy">立即下单{{i.pid}}</div>-->
      <!--</router-link>-->
      <div class="buy" @click="orderSubmit">立即下单</div>
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
export default {
  data: function () {
    return {
      num1: 1,
      comDatas: [], // 商品data
      smDatas: [],  // 套餐data
      currSM: [],   // 當前套餐
      smString: [],   // 選中后的套餐string
      price: [],
      hb: [],
      email : [],
      line : []
    };
  },
  created () {
    this.getComData();
  },
  methods: {
    back () {
      this.$router.go(-1);
    },
    handleChange (value) {
      console.log(value);
    },
    getComData () {
      var http = 'http://cod.hzhanghuan.com';
      var idArr = window.location.search.split("=");

      this.$axios.get( http+"/api/v1/Cod/goodsdetail?id="+idArr[1])
      // this.$axios.get(http + "/api/v1/Cod/goodsdetail?id=54")
        .then(response => {
          var commodityData = response.data.data;
          // console.log(commodityData);
          this.comDatas = commodityData;
          // 属性
          this.smDatas = commodityData.setmeals;
          // 缩略图
          var thumbnailImg = commodityData.thumbnail;
          var thumbnailHtml = '<img src="' + http + thumbnailImg + '">';
          $('.thumbnail-img').append(thumbnailHtml);
          // 货币
          this.hb = commodityData.currency_front_symbol;
          // email
          this.email = commodityData.email;
          // line
          this.line = commodityData.LINE;
        });
    },
    selectSetMeal (index) {  // 选择套餐
      var that = this;
      var http = 'http://cod.hzhanghuan.com';

      this.current = index; // 当前套餐下标
      var currIndex = this.current;

      var smDatas = this.smDatas; // 当前套餐下的属性
      var currSM = smDatas[currIndex];
      that.currSM = currSM;
      $(".sm-price span").text(currSM.setprice);
      that.price = currSM.setprice;
      var $thisAttr = '';
      // $thisAttr += '<div class="attr-title">'+currSM.setname+'</div>';
      $thisAttr += '<ul class="attr-img-list">';
      $thisAttr += '</ul>';

      $(".attr-box").empty();
      $(".attr-box").append($thisAttr);
      var attrChildIndex = currSM.properties.length;

      if (attrChildIndex >= 1) { // 如果套餐里面包含属性时

        for (var i in currSM.properties) {
          var img = http + currSM.properties[i].imageimage; // 图片
          var imgHtml = '<li class="attr-img-li" title="' + currSM.properties[i].rel_id + '"><img src=' + img + ' alt=""><span>' + currSM.properties[i].chinese_name + '</span></li>';
          $('.attr-img-list').append(imgHtml);

        }
        $(".attr-img-li").on('click', function () {  // 点击属性
          $(this).toggleClass("click-attr");
          $(this).siblings().removeClass("click-attr");
          var rel_id = $(this).attr('title');
          that.smString = 'rel_id=' + rel_id;
        })

      } else if (attrChildIndex == 0) {
        that.smString = 'rel_id=' + currSM.rel_id;
      }
    },

    orderSubmit () {  // 套餐提交
      var smArr = this.smString;
      var price = this.price;
      var hb = this.hb;
      var num = this.num1;
      var email = this.email;
      var line = this.line;
      if (smArr.length == 0){
        this.$message('請選擇套餐!');
        return;
      }
      this.$router.push({ name: 'form', params: { smString: smArr, price: price, hb: hb, num: num,email:email ,line:line} });
    }
  }

}
</script>

<style>
a {
  text-decoration: none;
  color: #333;
}
.set-meal-title {
  height: 45px;
  line-height: 45px;
  text-align: center;
  box-shadow: 0 2px 6px #f6f6f6;
  margin-bottom: 5px;
}
.back-icon {
  float: left;
  margin-left: 15px;
  margin-top: 15px;
}
.format {
  padding: 10px 0;
  width: 95%;
  margin: 0 auto;
}
.package {
  margin-top: 10px;
}
.set-meal-box {
  margin: 15px 0;
}
.buy {
  background-color: #409eff;
  color: #fff;
  width: 100%;
  height: 40px;
  line-height: 40px;
  text-align: center;
  border-radius: 4px;
}
.package-title {
  color: #a2a2a2;
  margin: 10px 0;
}
.attr-title {
  font-size: 16px;
  color: #666666;
}
.attr-name {
  font-weight: bold;
  color: #333;
}
.attr-img-list {
  overflow: hidden;
  margin: 20px;
}
.attr-img-li {
  width: 100px;
  height: 120px;
  float: left;
  margin-right: 15px;
  text-align: center;
  cursor: pointer;
}
.attr-img-li img {
  display: block;
  width: 80px;
  height: 80px;
  margin: 10px 10px;
  border: 1px solid #c4c4c4;
}

.attr-img-li span {
  color: #333;
  font-size: 14px;
}
/*// 缩略图*/
.thumbnail-box {
  width: 98%;
  margin: 20px auto;
}
.thumbnail-img {
  width: 100px;
  height: 100px;
}
.thumbnail-img img {
  width: 100px;
  height: 100px;
}
.sm-price {
  color: #333333;
  line-height: 52px;
}
.sm-price span {
  font-size: 25px;
  color: #333333;
  font-weight: bold;
}

.click-sm {
  color: #409eff;
  border-color: #c6e2ff;
  background-color: #ecf5ff;
}
.click-attr {
  color: #409eff;
  border-color: #c6e2ff;
}
.click-attr img {
  border-color: #c6e2ff;
}
.click-attr span {
  color: #409eff;
  background-color: #ecf5ff;
}
</style>




